/* Container centers the chatbot */
.chatbot-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background-color: #f0f2f5;
  min-height: 100vh;
}

/* Card structure with soft shadows */
.chatbot-card {
  width: 100%;
  max-width: 700px;
  height: 600px;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Header styling */
.chatbot-header {
  background: linear-gradient(to right, #1a73e8, #4285f4);
  color: white;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.25rem;
  font-size: 1.15rem;
  font-weight: 600;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

/* Message box scrolling area */
.chatbot-messages {
  flex: 1;
  padding: 1.25rem;
  overflow-y: auto;
  background: #f9fafb;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  scrollbar-width: none;
}

.chatbot-messages::-webkit-scrollbar {
  display: none;
}

/* Common chat message bubble style */
.chatbot-message {
  max-width: 80%;
  padding: 0.9rem 1.1rem;
  border-radius: 1.25rem;
  font-size: 0.95rem;
  line-height: 1.6;
  white-space: pre-wrap;
  word-break: break-word;
}

/* User bubble (right aligned) */
.user-message {
  background: #1a73e8;
  color: white;
  align-self: flex-end;
  text-align: left;
  border-bottom-right-radius: 0;
}

/* Assistant bubble (left aligned) */
.assistant-message {
  background: #e4eaf1;
  color: #202124;
  align-self: flex-start;
  border-bottom-left-radius: 0;
}

/* Input wrapper */
.chatbot-input-area {
  padding: 1rem 1.25rem;
  border-top: 1px solid #e0e0e0;
  background: #fff;
}

/* Input field and button */
.chatbot-input-box {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.chatbot-input {
  flex: 1;
  font-size: 0.95rem;
  padding: 0.6rem 0.9rem;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  outline: none;
  transition: 0.2s ease;
}

.chatbot-input:focus {
  border-color: #1a73e8;
  box-shadow: 0 0 0 2px rgba(26, 115, 232, 0.2);
}

.chatbot-send-btn {
  background: #1a73e8;
  color: white;
  border: none;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.25s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chatbot-send-btn:disabled {
  background: #90caf9;
  cursor: not-allowed;
}

.chatbot-send-btn:hover:enabled {
  background: #1669c1;
}

/* Loader (three dot pulse) */
.chatbot-loader {
  display: flex;
  gap: 6px;
  padding: 0.5rem;
  align-items: center;
}

.chatbot-loader div {
  width: 8px;
  height: 8px;
  background-color: #888;
  border-radius: 50%;
  animation: pulse 1s infinite ease-in-out;
}

.chatbot-loader div:nth-child(2) {
  animation-delay: 0.2s;
}

.chatbot-loader div:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.3);
  }
}
