/* Navbar.css */

.navbar {
  backdrop-filter: blur(16px);
  background: linear-gradient(90deg, rgba(30,64,175,0.85) 0%, rgba(99,102,241,0.85) 100%);
  color: #fff;
  box-shadow: 0 6px 24px rgba(30, 64, 175, 0.12);
  border-bottom: 1px solid rgba(255,255,255,0.12);
  position: sticky;
  top: 0;
  z-index: 999;
  font-family: 'Segoe UI', 'Inter', 'Roboto', sans-serif;
  transition: background 0.3s;
}

.navbar-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 2rem;
  height: 4.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-title {
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 0.02em;
  background: linear-gradient(90deg, #fff 60%, #a5b4fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.navbar-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.navbar-btn {
  background: rgba(255,255,255,0.08);
  border: none;
  padding: 0.6rem 1.2rem;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 0.75rem;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(30,64,175,0.08);
  transition: background 0.2s, box-shadow 0.2s, color 0.2s;
}

.navbar-btn:hover,
.navbar-btn.active {
  background: rgba(255,255,255,0.18);
  color: #e0e7ff;
  box-shadow: 0 4px 16px rgba(99,102,241,0.12);
}

.navbar-user {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1rem;
}

.navbar-user button {
  background: rgba(255,255,255,0.08);
  border: none;
  color: #fff;
  cursor: pointer;
  padding: 0.5rem 0.7rem;
  border-radius: 0.5rem;
  transition: background 0.2s, color 0.2s;
}

.navbar-user button:hover {
  background: rgba(255,255,255,0.18);
  color: #a5b4fc;
}

/* Mobile Menu */
.mobile-menu-btn {
  background: rgba(255,255,255,0.08);
  border: none;
  color: #fff;
  padding: 0.6rem;
  border-radius: 0.5rem;
  transition: background 0.2s, color 0.2s;
}

.mobile-menu-btn:hover {
  background: rgba(255,255,255,0.18);
  color: #a5b4fc;
}

.mobile-menu {
  background: linear-gradient(90deg, rgba(30,64,175,0.95) 0%, rgba(99,102,241,0.95) 100%);
  backdrop-filter: blur(12px);
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 8px 32px rgba(30,64,175,0.18);
}

.mobile-menu button {
  width: 100%;
  text-align: left;
  padding: 1rem 1.2rem;
  background: rgba(255,255,255,0.08);
  border: none;
  font-size: 1.1rem;
  font-weight: 500;
  color: #fff;
  border-radius: 0.75rem;
  margin-bottom: 0.75rem;
  transition: background 0.2s, color 0.2s;
}

.mobile-menu button:hover,
.mobile-menu button.active {
  background: rgba(255,255,255,0.18);
  color: #e0e7ff;
}

.mobile-menu .user-section {
  border-top: 1px solid rgba(255,255,255,0.18);
  margin-top: 1.2rem;
  padding-top: 1.2rem;
  font-size: 1rem;
}

.mobile-menu .signout-btn {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  color: #ffe4e6;
  margin-top: 0.7rem;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.2s;
}
.mobile-menu .signout-btn:hover {
  color: #f87171;
}